.intro {
  background: #283645 url("../img/bg-back-mobile@1x.jpg") center top no-repeat;
  background-size: cover;
}

.intro__inner {
  position: relative;
  width: auto;
  padding: 0;

  &::before {
    content: "";

    position: relative;
    bottom: 0;

    display: block;
    width: 320px;
    height: 396px;
    margin: 0;
    margin-left: auto;
    margin-right: auto;

    background: url("../img/iphone-hand-mobile@1x.png") left bottom no-repeat;
  }
}

@media (min-width: 660px) {
  .intro {
    background: #283645 url("../img/bg-back-tablet@1x.jpg") center top no-repeat;
    background-size: cover;
  }

  .intro__inner::before {
    z-index: 1;
    width: 100%;
    height: 545px;

    background: url("../img/iphone-hand-tablet@1x.png") right 100px no-repeat;
    background-size: 660px auto;
  }
}

@media (min-width: 1200px) {
  .intro {
    background: #283645 url("../img/bg-back-desktop@1x.jpg") center top no-repeat;
    background-size: cover;
  }

  .intro__inner::before {
    z-index: 1;
    width: 100%;
    height: 645px;

    background: url("../img/iphone-hand-desktop@1x.png") right 100px no-repeat;
    background-size: 1050px auto;
  }
}
